<template>
  <div class="record mt-5">
    <div class="p-5 mx-auto">
      <div class="d-flex justify-content-center">
        <div class="p-2 left-btn" @click="frame = 'exchange'" :class="{'btn-checked':frame === 'exchange'}">點數兌換紀錄</div>
        <div class="p-2 right-btn" @click="frame = 'history'" :class="{'btn-checked':frame === 'history'}">點數累積紀錄</div>
      </div>
      <div v-if="frame === 'exchange'" class="p-2 border">
        日期exchangeexchange
        <button @click="usecoupon()">兌換</button>
      </div>
      <div v-if="frame === 'history'" class="p-2 border">
        xu3historyhistory
      </div>
    </div>
    <div v-if="modalframe" class="frame-modal d-flex align-items-center justify-content-center">
      <div class="p-5 bg-white border rounded">
        <p @click="modalframe = false">close</p>
        <img src="../assets/images/qrcode.png" width="200px" alt="" class="border">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'record',
  data () {
    return {
      frame: 'exchange',
      modalframe: false
    }
  },
  methods: {
    usecoupon () {
      this.modalframe = true
    }
  }
}
</script>

<style>

</style>



